<template>
  <div class="rechargeBTC-box">
    <div class="withdrawal-bar">
      <van-nav-bar
        :title="title"
        left-arrow
        @click-left="linkHome"
      />
    </div>
    <div class="recharge-content-box">
      <div class="recharge-content-address" v-for="(item,index) in list ">
        <div>{{item}}</div>
        <div style="margin:10px 0;color: #1988FF;" @click="copyNumber(item)">{{$t('CP')}}</div>
        <vue-qr :text="item" :margin="0" colorDark="#000000" colorLight="#fff"  :size="140"></vue-qr>
      </div>
    </div>
  </div>
</template>

<script>
  import { Toast } from 'vant';
  import {handleClipboard} from '../../../utils/clipboard'
  import vueQr from 'vue-qr'
  export default {
    name: 'rechargeBTC',
    data(){
      return{
        title:'',
        list:''
      }
    },
    components: {
      vueQr
    },
    created () {
      this.title = this.$route.params.title
      this.list = this.$route.params.data
    },
    methods:{
      // 复制
      copyNumber (link) { // 物流单号复制
        handleClipboard(link, event, () => {
          alert(this.$t('CP1'))
        }, () => {
          alert(this.$t('CP2'))
        })
      },
      // 返回上级
      linkHome () {
        this.$router.push({
          name:'recharge'
        })
      },
    }
  }
</script>

<style scoped>

</style>



